// تنفيذ القائمة المتحركة للموبايل
document.addEventListener('DOMContentLoaded', function() {
    // عناصر القائمة المتحركة
    const menuToggle = document.querySelector('.menu-toggle');
    const navLinks = document.querySelector('.nav-links');
    
    if (menuToggle) {
        menuToggle.addEventListener('click', function() {
            navLinks.classList.toggle('active');
            menuToggle.querySelector('i').classList.toggle('fa-bars');
            menuToggle.querySelector('i').classList.toggle('fa-times');
        });
        
        // إغلاق القائمة عند النقر على رابط
        const navItems = document.querySelectorAll('.nav-links a');
        navItems.forEach(item => {
            item.addEventListener('click', function() {
                navLinks.classList.remove('active');
                menuToggle.querySelector('i').classList.add('fa-bars');
                menuToggle.querySelector('i').classList.remove('fa-times');
            });
        });
    }
    
    // تنفيذ السلايدر
    const slider = {
        slides: document.querySelectorAll('.slide'),
        dots: document.querySelectorAll('.dot'),
        prevBtn: document.querySelector('.prev-slide'),
        nextBtn: document.querySelector('.next-slide'),
        currentSlide: 0,
        slideInterval: null,
        
        init: function() {
            // عرض الشريحة الأولى
            this.showSlide(this.currentSlide);
            
            // إضافة الأحداث للأزرار
            if (this.prevBtn) {
                this.prevBtn.addEventListener('click', () => {
                    this.prevSlide();
                });
            }
            
            if (this.nextBtn) {
                this.nextBtn.addEventListener('click', () => {
                    this.nextSlide();
                });
            }
            
            // إضافة الأحداث للنقاط
            this.dots.forEach((dot, index) => {
                dot.addEventListener('click', () => {
                    this.showSlide(index);
                });
            });
            
            // بدء التشغيل التلقائي
            this.startAutoSlide();
            
            // إيقاف التشغيل التلقائي عند التمرير
            document.querySelector('.slider-container').addEventListener('mouseenter', () => {
                this.stopAutoSlide();
            });
            
            document.querySelector('.slider-container').addEventListener('mouseleave', () => {
                this.startAutoSlide();
            });
        },
        
        showSlide: function(index) {
            // إخفاء جميع الشرائح
            this.slides.forEach(slide => {
                slide.classList.remove('active');
            });
            
            // إزالة النشاط من جميع النقاط
            this.dots.forEach(dot => {
                dot.classList.remove('active');
            });
            
            // عرض الشريحة المطلوبة
            this.slides[index].classList.add('active');
            this.dots[index].classList.add('active');
            this.currentSlide = index;
        },
        
        nextSlide: function() {
            let nextIndex = (this.currentSlide + 1) % this.slides.length;
            this.showSlide(nextIndex);
        },
        
        prevSlide: function() {
            let prevIndex = (this.currentSlide - 1 + this.slides.length) % this.slides.length;
            this.showSlide(prevIndex);
        },
        
        startAutoSlide: function() {
            this.slideInterval = setInterval(() => {
                this.nextSlide();
            }, 5000);
        },
        
        stopAutoSlide: function() {
            if (this.slideInterval) {
                clearInterval(this.slideInterval);
            }
        }
    };
    
    // تهيئة السلايدر إذا كان موجودًا
    if (document.querySelector('.slider')) {
        slider.init();
    }
    
    // تأثيرات التمرير للروابط
    const navLinksAll = document.querySelectorAll('.navbar a');
    
    navLinksAll.forEach(link => {
        link.addEventListener('click', function(e) {
            const href = this.getAttribute('href');
            
            if (href.startsWith('#')) {
                e.preventDefault();
                
                const targetId = href.substring(1);
                const targetElement = document.getElementById(targetId);
                
                if (targetElement) {
                    window.scrollTo({
                        top: targetElement.offsetTop - 80,
                        behavior: 'smooth'
                    });
                }
            }
        });
    });
    
    // تأثيرات الظهور عند التمرير
    const animateOnScroll = function() {
        const elements = document.querySelectorAll('.service-card, .value-item, .step, .info-item');
        
        elements.forEach(element => {
            const elementTop = element.getBoundingClientRect().top;
            const elementVisible = 150;
            
            if (elementTop < window.innerHeight - elementVisible) {
                element.style.opacity = '1';
                element.style.transform = 'translateY(0)';
            }
        });
    };
    
    // تعيين القيم الأولية للعناصر المتحركة
    const animatedElements = document.querySelectorAll('.service-card, .value-item, .step, .info-item');
    animatedElements.forEach(element => {
        element.style.opacity = '0';
        element.style.transform = 'translateY(30px)';
        element.style.transition = 'opacity 0.6s, transform 0.6s';
    });
    
    // تشغيل تأثيرات الظهور عند التمرير
    window.addEventListener('scroll', animateOnScroll);
    
    // تشغيلها مرة عند التحميل
    animateOnScroll();
    
    // نموذج الاتصال
    const contactForm = document.getElementById('messageForm');
    
    if (contactForm) {
        contactForm.addEventListener('submit', function(e) {
            e.preventDefault();
            
            // جمع بيانات النموذج
            const name = this.querySelector('input[type="text"]').value;
            const phone = this.querySelector('input[type="tel"]').value;
            const service = this.querySelector('select').value;
            const message = this.querySelector('textarea').value;
            
            // نص رسالة واتساب
            const whatsappMessage = `مرحباً، أريد طلب خدمة%0A%0A`
                + `الاسم: ${name}%0A`
                + `رقم الهاتف: ${phone}%0A`
                + `الخدمة المطلوبة: ${this.querySelector('select option:checked').textContent}%0A`
                + `تفاصيل الطلب: ${message}`;
            
            // فتح واتساب مع الرسالة
            window.open(`https://wa.me/21612345678?text=${whatsappMessage}`, '_blank');
            
            // إعادة تعيين النموذج
            this.reset();
            
            // عرض رسالة نجاح
            alert('تم فتح تطبيق واتساب، يرجى إرسال الرسالة لإكمال الطلب');
        });
    }
    
    // تغيير لون شريط التنقل عند التمرير
    window.addEventListener('scroll', function() {
        const navbar = document.querySelector('.navbar');
        if (window.scrollY > 50) {
            navbar.style.boxShadow = '0 5px 20px rgba(0, 0, 0, 0.1)';
            navbar.style.backgroundColor = 'rgba(255, 255, 255, 0.98)';
        } else {
            navbar.style.boxShadow = '0 2px 15px rgba(0, 0, 0, 0.1)';
            navbar.style.backgroundColor = 'var(--secondary-color)';
        }
    });
});